#menuDiv{
  width: 130px;
  height: 100%;
  background: black;
  float: left;
  position: fixed;
  z-index: 1;
}

#artistName {
  color: white;
  font-size: 31px;
  transform: rotate(-90deg);
  margin-top: 80px;
}

svg{
  color: white;
  font-size:25px;
  margin-top: 20px;
  cursor: pointer;
}

.Socialicons {
  width: 100%;
  text-align: -webkit-center;
  text-align: -moz-center;
  margin-top: 100px;
}

#HomeTab{
  width:100%;
  height:100%;
  float:left;
  position:fixed;
  text-align: -webkit-center;
  text-align: -moz-center;
}

#poster{
  width:50%;
  height:100%;
  background: url(./Images/summerdays.jpg);
  background-size: cover;
  margin-left: 100px;
  animation: slider 20s infinite;
}

@keyframes slider{

  24.9999%{
    background: url(./Images/summerdays.jpg);
    background-size: cover;
  }

  25%{
    background: url(./Images/kfu.jpg);
    background-size: cover;
  }

  49.9999%{
    background: url(./Images/kfu.jpg);
    background-size: cover;
  }

  50%{
    background: url(./Images/sds.jpg);
    background-size: cover;
  }

  74.9999%{
    background: url(./Images/sds.jpg);
    background-size: cover;
  }

  75%{
    background: url(./Images/mm.jpg);
    background-size: cover;
  }

  100%{
    background: url(./Images/mm.jpg);
    background-size: cover;
  }
}


#posterInfo{
  margin-left: 185px;
  margin-top: 200px;
  position: absolute;
}

#posterInfo h1{
  margin-top: 0px;
  font-size: 100px;
  /* font-weight: lighter; */
  margin-bottom: 0px;
}


button {
  width: 200px;
  height: 45px;
  border: 1px solid;
  background: transparent;
  cursor: pointer;
  outline: none;
  font-size: 25px;
  font-weight: bold;
}

#cirlceDot {
  width: 100%;
  height: 40px;
  text-align: -webkit-center;
  display: flex;
}

#cirlceDot div {
  width: 5px;
  height: 5px;
  border: 1px solid gray;
  margin-left: 5px;
  border-radius: 5px;
}

#circle1{
  background: gray;
}